/* Styles for components */
.component {
  margin: 20px auto; /* Center the component horizontally */
  padding: 20px;
  border: 2px solid #007bff;
  border-radius: 8px;
  background-color: #f0f8ff;
  text-align: center; /* Center align the content within the component */
}

/* Styles for function component tables */
.function-component {
  width: 80%; /* Adjust the width as needed */
  margin: 0 auto; /* Center the table horizontally */
  border-collapse: collapse;
}

/* Styles for class component tables */
.class-component {
  width: 80%; /* Adjust the width as needed */
  margin: 0 auto; /* Center the table horizontally */
  border-collapse: collapse;
}

/* Styles for table headers */
th {
  border: 1px solid #007bff;
  padding: 8px;
}

/* Styles for table data */
td {
  border: 1px solid #007bff;
  padding: 8px;
}

.center-table {
  width: 80%; /* Adjust the width as needed */
  margin: 0 auto; /* Center the table horizontally */
  border-collapse: collapse;
}